/*
 * Font Face
 */
@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: local('../fonts/materialIcons/Material Icons'), local('../fonts/materialIcons/MaterialIcons-Regular'), url("../fonts/materialIcons/MaterialIcons-Regular.woff2") format('woff2'), url("../fonts/materialIcons/MaterialIcons-Regular.woff") format('woff'), url("../fonts/materialIcons/MaterialIcons-Regular.ttf") format('truetype');
}
.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  font-feature-settings: 'liga';
}
@font-face {
  font-family: 'Lato-Bold';
  src: url('../fonts/lato/Lato-Bold.ttf') format('truetype');
}
@font-face {
  font-family: 'Lato-BoldItalic';
  src: url('../fonts/lato/Lato-BoldItalic.ttf') format('truetype');
}
@font-face {
  font-family: 'Lato-Heavy';
  src: url('../fonts/lato/Lato-Heavy.ttf') format('truetype');
}
@font-face {
  font-family: 'Lato-HeavyItalic';
  src: url('../fonts/lato/Lato-HeavyItalic.ttf') format('truetype');
}
@font-face {
  font-family: 'GentiumBk-Regular';
  src: url('../fonts/gentium/GenBkBasR.ttf') format('truetype');
}
@font-face {
  font-family: 'GentiumBk-RegularItalic';
  src: url('../fonts/gentium/GenBkBasI.ttf') format('truetype');
}
@font-face {
  font-family: 'GentiumBk-Bold';
  src: url('../fonts/gentium/GenBkBasB.ttf') format('truetype');
}
@font-face {
  font-family: 'GentiumBk-BoldItalic';
  src: url('../fonts/gentium/GenBkBasBI.ttf') format('truetype');
}
body {
  -webkit-font-feature-settings: 'liga' 1, 'kern';
  font-feature-settings: 'liga' 1, 'kern';
  background: #F3F3F3;
  font-family: Helvetica, Tahoma, Arial, STXihei, "华文细黑", Heiti, "黑体", "Microsof YaHei", "微软雅黑", SimSun, "宋体", sans-serif;
  color: #707070;
}
.preview {
  word-wrap: break-word;
}
