* Access Denied

.access_denied {
  background-image: none;
}

.access_denied img {
  border: 1px dashed gray;
  border-radius: 7px;
  margin: 10px 0;
  padding: 30px 60px;
}


* Lists

ul,
ol {
  padding: 0 0 30px 28px;
}


* Quotes

.cm-quote {
  margin: 0 0 30px 0;
  padding: 0 0 0 15px;
  border-left: 2px solid rgb(0, 0, 0);
}

blockquote p:last-child {
  padding: 0;
}


* Code

pre {
  white-space: pre-wrap;
  font: 16px/28px Menlo, Monaco, monospace, serif;
  padding: 15px;
  margin: 0 0 30px 0;
}

.cm-comment {
  padding: 2px;
  font: 16px/28px Menlo, Monaco, monospace, serif;
}

